.present {
	padding-top: 155px;
	@include clr;
	&__title {
		color: #fff;
		max-width: 905px;
		margin: 0 auto 70px;
		text-align: center;
		@include evo;
		line-height: 1;
		@include fluid-font(38px, 48px, 900px, 1140px, 48px);
		span {
			@include fluid-font(56px, 72px, 900px, 1140px, 72px);
			margin-top: 24px;
			display: block; } }
	&__col1 {
		width: 40%;
		float: left;
		padding-right: 62px; }
	&__col2 {
		width: 60%;
		float: left;
		position: relative; } }
