.attention {
	@include evo;
	line-height: 1;
	max-width: 1063px;
	margin: auto;
	padding-top: 15px;
	padding-left: 426px;
	min-height: 315px;
	position: relative;
	&__pic {
		width: 337px;
		height: 315px;
		z-index: 1;
		position: absolute;
		top: 0;
		left: 44px;
		img {
			position: absolute;
			top: ((-28 / 337) * 100) * "1%";
			right: ((-44 / 315) * 100) * "1%";
			bottom: ((-28 / 337) * 100) * "1%";
			left: ((-44 / 315) * 100) * "1%";
			margin: auto;
			width: auto; } }
	&__title {
		@include fluid-font(38px, 48px, 900px, 1140px, 48px);
		color: #2e2e2e;
		margin-bottom: 32px;
		span {
			@include fluid-font(30px, 36px, 900px, 1140px, 36px);
			display: block; } }
	&__subtitle {
		@include fluid-font(24px, 30px, 900px, 1140px, 30px);
		span {
			@include fluid-font(49px, 72px, 900px, 1140px, 72px);
			color: #ff7200;
			display: block;
			position: relative;
			margin-left: -43px;
			margin-bottom: 10px; } } }
